import React, { useEffect } from "react";
import "../../../../styles/components/homePage/Introduce.scss";

import WOW from "wowjs";

function Introduce() {
  useEffect(() => {
    const wow = new WOW.WOW();
    wow.init();
  });

  return (
    <div className="introduce">
      <h1
        className="introduce--title wow slideInRight"
        data-wow-offset="10"
        data-wow-iteration="10"
      >
        Vinaseed l oan nghip c lp trc thuc B Nng Nghip v Pht Trin
        Nng thn
      </h1>

      <p className="introduce--text">
        c thnh lp t nm 1968. Vi s i mi quan tri DN, chin lc ly
        KHCN lam nn tang va ng lc nng cao nng lc canh tranh, huy ng ti
        a cac ngun lc xa hi cung tham gia pht trin cng ty, sau 15 nm
        CPH, Vinaseed  tr thnh Tp on nng nghip c quy m v th phn
        ln nht Vit Nam.
      </p>
    </div>
  );
}

export default Introduce;
