import React, { useState, useEffect } from "react";

import Slider from "react-slick";

//style
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

//images
import Img1 from "../../../../assets/images/tech/tech-1.jpg";
import Img2 from "../../../../assets/images/tech/tech-2.jpg";
import Img3 from "../../../../assets/images/tech/tech-3.jpg";

//icons
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";

function Technology() {
  let carousel = [
    {
      id: 0,
      image: Img1,
      title: "cng ngh chn to ging cy trng",
      text:
        "Vinaseed l doanh nghip KHCN u tin trong ngnh ging cy trng Vit Nam, tin phong trong hot ng nghin cu, ng dng v chuyn giao cng ngh vi cc gii php t ph, i trc v nh hng th trng. H thng c s h tng phc v nghin cu hin i, i ng chuyn gia hng hu, nhn s trn 1000 lao ng vi 80% c trnh  i hc v trn i hc hon ton lm ch cng ngh chn to ging tin tin trn th gii, cng ngh sn xut ht lai, cng ngh sn xut nng nghip 4.0.",
    },

    {
      id: 1,
      image: Img2,
      title: "qun tr sn xut nng nghip thng minh 4.0",
      text:
        "Xy dng phng thc qun tr sn xut nng nghip tin tin,  ng dng cc gii php canh tc bn vng tit kim ti nguyn, c truy xut ngun gc, s dng cc ch phm v thuc BVTV c ngun gc sinh hc th h mi gp phn gim hiu ng nh knh, gim tn d ha cht v hm lng Nitorat trong sn phm, thc hin c gii ha trong sn xut v ch bin.  H tr cc doanh nghip va v nh, cc HTX, h nng dn tham gia chui lin kt sn xut nng sn ca Vinaseed  hnh thnh cc vng sn xut nng nghip tp trung quy m ln, gp phn em li thu nhp n nh cho ngi nng dn.",
    },

    {
      id: 2,
      image: Img3,
      title: "cng ngh sn xut hin i",
      text:
        "Thc hin chin lc u t  m rng cng ty, i mi cng ngh nng cao nng lc cnh tranh, Vinaseed  xy dng cc trung tm cng nghip ch bin ht ging v ch bin nng sn quy m ln, hin i, ng b v t ng ha hng u ti Vit Nam, cc dng sn phm ca nh my sn xut ra p ng cc quy trnh kim nh kht khe theo tiu chun quc t, an ton vi con ngi v mi trng.",
    },
  ];

  const [slideNum, setSlideNum] = useState();
  const [slideTitle, setSlideTitle] = useState();
  const [slideText, setSlideText] = useState();

  const handleSlide = (clicked) => {
    clicked.onClick();
    setSlideNum(clicked.currentSlide);
    handleText(clicked.currentSlide);
  };

  const handleText = (id) => {
    carousel.map(function (num, i) {
      if (id === num.id) {
        setSlideText(num.text);
        setSlideTitle(num.title);
      }
    });
  };

  const handlePaging = (pageNum) => {
    pageNum.map(function (num, i) {
      if (num.props.className === "slick-active") {
        setSlideNum(i);
        handleText(i);
      }
    });
  };

  function Arrow(props) {
    let className = props.type === "next" ? "nextArrow" : "prevArrow";
    className += " arrow";
    const char =
      props.type === "next" ? <NavigateNextIcon /> : <NavigateBeforeIcon />;
    return (
      <span className={className} onClick={() => handleSlide(props)}>
        {char}
      </span>
    );
  }

  function customPaging(i) {
    return <span className="test">0{i + 1}</span>;
  }

  function appendDots(dots) {
    return (
      <div className="tech-paging" onClick={handlePaging(dots)}>
        <ul className="tech-paging__numb"> {dots} </ul>
      </div>
    );
  }

  const renderSlides = () =>
    carousel.map((num) => (
      <div className="cus-slide">
        <div className="cus-slide__img">
          <img src={num.image} alt="" />
        </div>
      </div>
    ));

  const renderText = () => (
    <div>
      <h4 className="technology__text--title-small">{slideTitle}</h4>

      <p className="technology__text--description">{slideText}</p>
    </div>
  );

  return (
    <div className="technology">
      <div className="technology__img">
        <Slider
          nextArrow={<Arrow type="next" />}
          prevArrow={<Arrow type="prev" />}
          dots={true}
          customPaging={customPaging}
          appendDots={appendDots}
        >
          {renderSlides()}
        </Slider>
      </div>

      <div className="technology__text">
        <h2 className="technology__text--title-main">cng ngh ni bt</h2>
        {renderText()}
      </div>
    </div>
  );
}

export default Technology;
